header {
  margin: auto;
  width: 1080px;
  height: 215px;
  display: flex;
  align-items: center;
  position: relative;
}

fieldset {
  border: none;
}

.visually-hidden {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  white-space: nowrap;
}

.links {
  position: absolute;
  top: 0;
  right: 0;
}

.link_text {
  font-size: 10px;
  margin-left: 5px;
}

.img_logo {
  margin-bottom: 12px;
  width: 220px;
}

.search_box {
  width: 520px;
  height: 50px;
  border: 2px solid #03cf5d;
  display: flex;
  align-items: center;
}

.search_box input {
  flex: 9;
  height: 46px;
  padding-left: 12px;
  padding-right: 12px;
  border: none;
  outline: none;
  font-size: 18px;
}

.search_box button {
  flex: 1;
  height: 46px;
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  background-color: #03cf5d;
  color: #fff;
}

header > nav {
  width: 100%;
  height: 45px;
  position: absolute;
  bottom: 0;
}

.nav_items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
  font-weight: bold;
  border-top: 1px solid #f1f3f6;
  width: 100%;
  height: 100%;
}

.nav_items ul > li {
  display: inline-block;
  margin-left: 8px;
}

.nav_items ul > li:nth-child(-n + 7) > a {
  color: #03cf5d;
}

.nav_items ul > li > a {
  text-decoration: none;
  cursor: pointer;
}
